.lpx-tree ul,
.lpx-tree li {
  position: relative; }

.lpx-tree ul {
  list-style: none; }
  .lpx-tree ul > li:last-child::after {
    height: 1rem; }

.lpx-tree li::before, .lpx-tree li::after {
  content: "";
  position: absolute;
  left: -.8rem; }

.lpx-tree li::before {
  border-top: 1px solid #ccc;
  top: 1.1rem;
  width: .6rem;
  height: 0; }

.lpx-tree li::after {
  border-left: 1px solid #ccc;
  height: 100%;
  width: 0px;
  top: .2rem; }
